<template>
  <div class="operation-center-query-search-wrap">
    <el-row class="breadcrumb-style">
      <el-breadcrumb separator="/">
        <el-breadcrumb-item :to="{ path: '/soc-query-log' }">运营中心</el-breadcrumb-item>
        <el-breadcrumb-item>日志查询</el-breadcrumb-item>
      </el-breadcrumb>
    </el-row>
    <el-row class="container-style">
      <div class="click-search-input-behave-map ">
        <div class="query-search-container">
          <div class="match-box" v-for="(item, index) in querySearchList" :key="index">
            <div class="match-box-content">
              <el-select v-model="item.field" placeholder="日志字段" size="mini">
                <el-option v-for="data in optionsColumn" :key="data" :label="data" :value="data" />
              </el-select>
              <el-select v-model="item.operation" placeholder="匹配方式" size="mini">
                <el-option v-for="data in optionsSelect" :key="data.value" :label="data.label" :value="data.value" />
              </el-select>
              <el-input placeholder="请输入查询语句" v-model="item.value" size="mini"></el-input>
            </div>
            <el-button @click.prevent="removeRuleMatchs(item, index)" size="mini">删除</el-button>
          </div>
          <el-button @click="addRuleMatchs(index)" plain type="primary" size="mini">新增</el-button>
        </div>
        <div class="query-time-container">
          <el-select v-model="valueTime" placeholder="Select" v-show="isShowSelectTime" @change="onChangeSelectTime"
            size="mini">
            <el-option v-for="item in optionTime" :key="item.value" :label="item.label" :value="item.value" />
          </el-select>
          <div v-show="!isShowSelectTime">
            <el-date-picker v-model="pickerTime" type="datetimerange" range-separator="-" start-placeholder="开始时间"
              end-placeholder="结束时间" @change="changeTimeline" size="mini" />
          </div>
          <el-button type="primary" icon="el-icon-search" @click="onChangeSearch" size="mini">查询</el-button>
        </div>

      </div>
      <div style="padding-top: 10px;display: flex; width: 100%;">
        <div style="min-width:60px;line-height: 28px;font-size: 12px;">显示字段：</div>
        <el-select v-model="columnValue" multiple placeholder="Select" style="width: 100%" size="mini">
          <el-option v-for="item in optionsColumn" :key="item" :label="item" :value="item" />
        </el-select>
      </div>
      <el-divider style="margin: 15px 0;" />
      <el-col :span="24" v-loading.fullscreen.lock="loadingPage">
        <el-row>
          <el-col :span="24" v-if="mapAttackerEntity.length == 0">
            <el-empty description="NO DATA" />
          </el-col>
          <el-col :span="24">
            <el-timeline class="timeline-box">
              <el-timeline-item v-for="(item, index) in mapAttackerEntity" :key="index" :timestamp="item.RequestTime"
                placement="top" size="large" color="#409eff">
                <div class="operation-behave-dialog-box">
                  <el-card shadow="hover" style="margin-left: 15px;">
                    <div class="operation-behave-item" v-for="(i, index) in columnValue" :key="index">
                      <span class="operation-behave-label">{{ i }}</span>
                      <div class="operation-behave-content" v-if="i == 'RequestContent'"
                        style="background-color: #f4f4f5;padding: 15px;">
                        <span>
                          <pre>{{item[i]}}</pre>
                        </span>
                      </div>
                      <div class="operation-behave-content" v-else>
                        <span v-if="optionsPre.indexOf(i) > -1">
                          <pre> {{ item[i] }}</pre>
                        </span>
                        <span v-else>{{ item[i] }}</span>
                      </div>
                    </div>

                  </el-card>
                </div>
              </el-timeline-item>
            </el-timeline>
          </el-col>

        </el-row>
        <el-pagination background layout="prev, pager, next" :total="count" :page-size="20"
          @current-change="onCurrentChange" v-model:currentPage="now_page" />
      </el-col>
    </el-row>
  </div>
</template>
<script>
import { mixin, JXAjax, formatterDateTime } from "../assets/scripts/common";
import { useRoute } from 'vue-router'
export default {
  mixins: [mixin],
  data() {
    return {
      loadingPage: false,
      loading: false,
      mapAttackerEntity: [],
      count: 0,
      now_page: 1,
      pickerTime: [],
      isShowSelectTime: true,
      valueTime: "1w",
      optionTime: [
        { value: '1h', label: '1小时' },
        { value: '1d', label: '24小时' },
        { value: '1w', label: '7天' },
        { value: '1m', label: '30天' },
        { value: 'default', label: '自定义' }
      ],
      valueSelect: "",
      optionsSelect: [
        { value: 'contains', label: '包含' },
        { value: 'prefix', label: '前缀匹配' },
        { value: 'suffix', label: '后缀匹配' },
        { value: 'equals', label: '等于' },
        { value: 'not_equals', label: '不等于' }
      ],
      querySearchList: [
        { field:"",operation: "", value :""}
      ],
      optionsColumn: [
        "Host",
        "RequestUuid",
        "WafNodeUUID",
        "UpstreamAddr",
        "UpstreamResponseTime",
        "UpstreamStatus",
        "Status",
        "ProcessTime",
        "RequestTime",
        "URI",
        "SrcIP",
        "RawRespBody",
        "IsoCode",
        "City",
        "WafModule",
        "WafPolicy",
        "WafAction",
        "WafExtra",
        "RequestContent",
      ],
      columnValue: [
        "Host",
        "URI",
        "SrcIP",
        "Status",
        "WafModule",
        "WafPolicy",
        "RequestContent",
      ],
      optionsPre: [
        "RawRespBody",
      ],
      uuid: '',
      host: '',
      uri:'',
      time: '',
      from_time: '',
      to_time:'',
    };
  },
  mounted() {
    var t = this;
    const route = useRoute()
    if (JSON.stringify(route.params) != '{}') {
      t.uuid = route.params.uuid;
      t.host = decodeURIComponent(route.params.host);
      t.uri = decodeURIComponent(route.params.uri);
      t.time = JSON.parse(route.params.time);
      t.valueTime = t.time.type;
      t.querySearchList[0].field = 'SrcIP';
      t.querySearchList[0].operation = 'equals';
      t.querySearchList[0].value = t.uuid;
      if (t.host != 'undefined') {
        t.querySearchList.push({
          field: "Host",
          operation: "equals",
          value: t.host,
        })
      }
      if (t.uri != 'undefined') {
        t.querySearchList.push({
          field: "URI",
          operation: "equals",
          value: t.uri,
        })
      }
      t.onChangeSearch()
    } else { 
      t.getDataConf();
    }
  },
  methods: {
    getDataConf() {
      var t = this;
      JXAjax(
        "post",
        "/waf/waf_get_sys_report_conf_conf",
        {},
        function (response) {
          t.loadingPage = false;
          t.logSource = response.data.message.report_conf;
          if (t.logSource == "false") {
            t.$message({
              duration: 0,
              showClose: true,
              message: "日志查询功能未配置，" + "<a href='/#/sys-report-conf' class='error-message-btn'>" + "点击前往配置" + "</a>",
              type: "warning",
              dangerouslyUseHTMLString: true,
            });
          } else {
            t.onChangeSearch();
          }

        },
        function () {
          t.loadingPage = false;
        },
        "no-message"
      );
    },
    getData(now_page) {
      var t = this;
      t.loadingPage = true;
      var getUrl = "/soc/soc_query_log";
      var postData = {
        sql_rules: t.querySearchList,
        from_time: t.from_time,
        to_time: t.to_time,
        page_number: now_page,
      }
      JXAjax(
        "post",
        getUrl,
        postData,
        function (response) {
          t.mapAttackerEntity = response.data.message;
          t.count = response.data.total_count;
          t.now_page = response.data.now_page;
          t.loadingPage = false
        },
        function () {
          t.loadingPage = false;
        },
        "no-message"
      );
    },
    onChangeSearch() { 
      this.onChangeSelectTime()
      this.getData(1)
    },
    onCurrentChange() {
      this.getData(this.now_page)
    },
    formatterLog(data) { 
      var t = this;
      var arr = [];
      var list = t.optionsColumn;
      data.forEach((item, index) => {
        var _json = {};
        for (var i = 0; i < item.length; i++) {
          _json[list[i]] = item[i]
        }
        arr.push(_json)
      });
      
      console.log(arr);
      return arr;
    },
    onChangeSelectTime() { 
      var t = this;
      if (t.valueTime == 'default') {
        t.isShowSelectTime = false;
        t.pickerTime = [
          new Date(new Date().getTime() - 24 * 60 * 60 * 1000),
          new Date()
        ];
      } else { 
        t.isShowSelectTime = true;
        if (t.valueTime == '1h') { 
          t.pickerTime = [
            new Date(new Date().getTime() - 1 * 60 * 60 * 1000),
            new Date()
          ];
        }
        if (t.valueTime == '1d') { 
          t.pickerTime = [
            new Date(new Date().getTime() - 24 * 60 * 60 * 1000),
            new Date()
          ];
        }
        if (t.valueTime == '1w') { 
          t.pickerTime = [
            new Date(new Date().getTime() - 24 * 60 * 60 * 1000 * 7),
            new Date()
          ];
        }
        if (t.valueTime == '1m') {
          t.pickerTime = [
            new Date(new Date().getTime() - 24 * 60 * 60 * 1000 * 30),
            new Date()
          ];
        }
      }
      if (t.time) {
        t.from_time = t.time.from_time;
        t.to_time = t.time.to_time;
      } else {
        t.from_time = formatterDateTime(t.pickerTime[0]);
        t.to_time = formatterDateTime(t.pickerTime[1]);
      }
    },
    changeTimeline(event) {
      var t = this;
      if (event == null) { 
        t.isShowSelectTime = true;
        t.valueTime = '1w'
      } else {
        t.isShowSelectTime = false;
      }
    },
    addRuleMatchs(index) {
      this.querySearchList.push({ operation: "", value: ""})
    },
    removeRuleMatchs(item, index) {
      if (index != -1 && this.querySearchList.length > 1) {
        this.querySearchList.splice(index, 1);
      }
    },
  },
};
</script>
<style>
.operation-center-query-search-wrap .el-checkbox{
  margin-right: 20px;
}
.operation-center-query-search-wrap .el-checkbox__label{
  font-size: 12px;
  padding-left: 5px;
}
.page-owasp-wrap .match-inline-block {
  width: 192px;
}

.engine-form .el-form-item__content {
  margin-left: 40px;
}

.page-owasp-wrap .global-pwd {
  width: calc(100% - 60px);
  margin-right: 4px;
}

.operation-behave-label {
  width: 160px;
  display: inline-block;
  text-align: right;
  padding: 0 20px 0 0;
  box-sizing: border-box;
}
.operation-behave-dialog-box p {
  display: inline-block;
}
.operation-behave-item{
  display: flex;
  padding: 10px 0;
}
.operation-behave-content{
  -webkit-box-flex: 1;
    -ms-flex: 1;
    flex: 1;
    position: relative;
    font-size: 14px;
    white-space: normal;
    word-break: break-all;
    word-wrap: break-word;
    
}
.operation-behave-content.button button:first-child{
  margin-right: 20px;
}

.operation-search-dialog-box .self-learn-change-audit-label {
  width: 180px;
  display: inline-block;
  text-align: right;
  line-height: 40px;
  padding: 0 12px 0 0;
  box-sizing: border-box;
}
.operation-search-dialog-box p {
  display: inline-block;
}
.self-learn-change-audit-item{
  display: flex;
}
.self-learn-change-audit-content{
  -webkit-box-flex: 1;
    -ms-flex: 1;
    flex: 1;
    line-height: 40px;
    position: relative;
    font-size: 14px;
}
.self-learn-change-audit-content div{
  display: block;
  line-height: 30px;
}

.timeline-box .el-timeline-item__wrapper{
  display:flex;
}
.timeline-box .el-timeline-item__content{
  position: relative;
  top: -8px;
}
.timeline-box .el-timeline-item__timestamp.is-top{
  font-size: 14px;
  color: #000;
}
.click-search-input-behave-map {
  width: 100%;
}
.operation-center-query-search-wrap .el-timeline-item__content{
  width: 100%;
}
.operation-center-query-search-wrap .timeline-box .el-timeline-item__timestamp{
  position: absolute;
  left: -150px;
}
.operation-center-query-search-wrap .el-timeline-item {
  margin-left: 150px;
}
.operation-center-query-search-wrap .el-timeline-item__wrapper {
  padding-left: 18px;
}
.operation-behave-content pre {
  white-space: pre-wrap;
  font-family: "Helvetica Neue", Helvetica, "PingFang SC", "Hiragino Sans GB",
    "Microsoft YaHei", "微软雅黑", Arial, sans-serif;
  font-size: 12px;
  color: rgb(48, 49, 51);
}
.query-search-container .el-input{
   width: auto;
}

.query-search-container .el-select {
  width: 120px;
  margin-right: 10px;
}
.query-search-container .match-box {
  display: inline-block;
  margin-bottom: 10px;
  margin-right: 10px;
}
.query-search-container .match-box:last-of-type {
  margin-right: 0px;
}

.query-search-container .el-button {
  margin-left: 10px;
}

.query-search-container .match-box-content {
  position: relative;
  display: inline-block;
}
.query-time-container{
  display: flex;
}
.query-time-container .el-button {
  margin-left: 10px;
}
.operation-center-query-search-wrap .operation-behave-dialog-box {
  margin-top: 15px;
}
.table-col-tag {
  margin: 5px;
  white-space: normal;
  height: auto;
}
</style>
